@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,700&display=swap);
:root {
	--unfocused: #777777;
	--focused: #eeeeee;
	--disabled: #3f3f3f;
}

body {
	margin: 0;
	font-family: "Open Sans Condensed", sans-serif;
	font-weight: 300;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: #e0eafc; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#cfdef3),
		to(#e0eafc)
	);
	background: linear-gradient(
		to right,
		#cfdef3,
		#e0eafc
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.bars-container {
	position: absolute;
	bottom: 0;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	width: 100%;
	padding: 0 5px 10px 5px;
	-webkit-transition: margin 0.3s ease;
	transition: margin 0.3s ease;
	white-space: nowrap;
	overflow: hidden;
}

.bar {
	background-color: green;
	display: inline-block;
	-webkit-transition: all 0.3s linear;
	transition: all 0.3s linear;
	margin: 0 1px;
	padding: 0;
}

.complete .bar {
	margin: 0 0px;
	padding: 0 1px;
	box-sizing: content-box;
}

.gradient-selector-container {
  position: relative;
}

.gradient-selector-btn {
  border-radius: 20px;
  background: transparent;
  width: 100%;
  height: 60px;
  padding: 5px;
  border: var(--unfocused) 1px solid;
  text-transform: uppercase;
  color: var(--unfocused);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.gradient-selector-btn h3 {
  font-size: 0.75rem;
  margin-bottom: 0;
  margin-top: 9px;
}

.gradient-selector-btn h4 {
  font-size: 0.5rem;
}

.gradient-selector-wrapper:hover .gradient-selector-btn {
  border-color: var(--focused);
  color: var(--focused);
  letter-spacing: 2px;
}

.gradient-selector-wrapper:hover .gradient-dropdown-list {
  border-color: var(--focused);
}

.gradient-selector-btn:hover {
  border-color: var(--focused);
  color: var(--focused);
  letter-spacing: 2px;
}

.gradient-selector-btn:hover ~ .gradient-dropdown-list {
  border-color: var(--focused);
  border-top: 0px;
}

.gradient-selector-btn:focus {
  outline: none;
}

.gradient-selector-btn:disabled {
  border-color: var(--disabled);
}

.gradient-dropdown-list {
  width: 100%;
  height: auto;
  position: absolute;
  display: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  border: var(--unfocused) 1px solid;
  border-top: 0px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  z-index: 7;
  padding-bottom: 20px;
}

.fadeIn {
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
}

@-webkit-keyframes fadeIn {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
  }
}

.show {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.show.gradient-dropdown-list {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  opacity: 1;
}

.dropIsShown {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  border-bottom: 0px;
}

.gradient-dropdown-option {
  flex-basis: 45%;
}

.gradient-dropdown-option input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.gradient-dropdown-option label {
  width: 60px;
  border-radius: 10px;
  border: solid 1px var(--unfocused);
  text-transform: uppercase;
  text-align: center;
  line-height: 3;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-size: 0.55rem;
  box-sizing: border-box;
}

.gradient-dropdown-option label:hover {
  border: solid 1px var(--focused);
  box-shadow: 0px 0px 0px 1px var(--focused);
  color: var(--focused);
  cursor: pointer;
}

.gradient-dropdown-option input:checked ~ label {
  font-weight: 700;
  border: solid 1px var(--focused);
  box-shadow: 0px 0px 0px 1px var(--focused);
  color: var(--focused);
}

@media screen and (min-width: 992px) {
  .gradient-selector-btn {
    height: 74.8px;
  }
  .gradient-selector-btn h3 {
    font-size: 1rem;
    margin-top: 10px;
  }
  .gradient-selector-btn h4 {
    font-size: 0.75rem;
  }
  .gradient-dropdown-option label {
    width: 140px;
    font-size: 0.85rem;
  }
  .gradient-dropdown-option label:hover {
    letter-spacing: 2px;
  }
}
